<template>
  <!-- section  -->
  <section v-if='showStatus || acceptOfferState || rejectOfferState' class="actions d-flex align-items-center justify-content-between">
    <app-button
      :label="acceptOfferState ? $t('offer.accepted') : $t('offer.accept')"
      class="m-auto action-btn"
      @click="respondOffer('accept')"
      :loading="
        respondActionLoading.type == 'accept' &&
        respondActionLoading.loading == true
      "
      :variant="acceptOfferState ? 'outline-success' : 'success'"
      :disabled="
        acceptOfferState ||
        (respondActionLoading.type != 'accept' &&
          respondActionLoading.loading == true)
      "
      v-if="!rejectOfferState"
    />
    <div v-if="!acceptOfferState && !rejectOfferState">
      <app-button
        :label="$t('offer.counterOffer')"
        variant="outline-warning"
        :class="['m-auto action-btn', { warning: counterOfferState }]"
        @click="showCounterModel = true"
      />
    </div>
    <div class="m-auto" v-if="!acceptOfferState">
      <span
        class="d-inline-block"
        tabindex="0"
        v-b-tooltip.hover="rejectOfferState"
        :title="
          rejectOfferState
            ? $t('offer.rejectionReason') + ' : ' + rejectionReason
            : ''
        "
      >
        <app-button
          :label="rejectOfferState ? $t('offer.rejected') : $t('offer.reject')"
          variant="outline-danger"
          :disabled="
            rejectOfferState ||
            (respondActionLoading.type != 'reject' &&
              respondActionLoading.loading == true)
          "
          class="m-auto action-btn"
          @click="rejectOfferModel = true"
        />
      </span>
    </div>

    <!-- :id="`counter-offer-modal-${offerId}`" -->
    <CounterOfferModal
      :id="`counter-offer-modal-${offerIndex}`"
      :tenant-offer-price="tenantOfferPrice"
      :offer-price-term="offerPriceTerm"
      @counterOffer="addCounterOffer"
      @reset-model="errorMessage = ''"
      ref="CounterOfferModal"
      :errorMessage="errorMessage"
      :showCounterModel.sync="showCounterModel"
    />

    <RejectOfferModal
      id="reject-offer-modal"
      :offer-id="offerId"
      @reject="rejectOffer"
      ref="RejectOfferModal"
      :rejectOfferModel.sync="rejectOfferModel"
    />
  </section>
</template>

<style lang="scss" scoped>
.actions {
  margin-bottom: 24px;
  .action-btn {
    min-width: 100px;
    height: 40px;
    padding: 11px 7px 11px 8px;
    text-align: center;
    line-height: 1;
  }
}

::v-deep() {
  .arrow,
  .arrow::before {
    display: none;
  }
  .tooltip-inner {
    background: #3d3d3d;
  }
}
</style>

<script>
import CounterOfferModal from '~/components/shared/CounterOfferModal'
import RejectOfferModal from '~/components/shared/RejectOfferModal'
import { createNamespacedHelpers } from 'vuex'
const { mapState, mapActions } = createNamespacedHelpers('PropertyModule')
export default {
  name: 'RespondActions',
  components: {
    CounterOfferModal,
    RejectOfferModal,
  },
  props: {
    tenantOfferPrice: {
      type: Number,
      required: false,
      default: 0,
    },
    offerPriceTerm: {
      type: Number | String,
      required: false,
    },
    offerId: {
      type: String | Number,
      required: true,
    },
    status: {
      type: Number | String,
      require: false,
      default: 3,
    },
    rejectionReason: {
      type: String,
    },
    offerIndex: {
      type: Number,
      default: 0,
    },
    showStatus: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      errorMessage: '',
      showCounterModel: false,
      rejectOfferModel: false,
    }
  },
  methods: {
    ...mapActions(['acceptRejectOffer']),
    respondOffer(respondType, form) {
      const payload = {}
      payload.offer_id = this.offerId
      payload.type = respondType
      if (form && form.notes) {
        payload.notes = form.notes
      }
      if (form && form.offer_price) {
        payload.offer_price = form.offer_price
      }
      if (form && form.rejection_object) {
        payload.rejection_id = form.rejection_object.rejection_id
        payload.rejection_reason = form.rejection_object.rejection_reason
      }
      this.acceptRejectOffer(payload)
        .then(() => {
          setTimeout(() => {
            this.$refs['CounterOfferModal'].resetModal()
            this.$refs['RejectOfferModal'].resetModal()
          }, 3000)
          setTimeout(() => {
            this.$emit('onSubmit')
          }, 1500)
          this.errorMessage = ''
        })
        .catch((err) => {
          this.errorMessage = err.response.data.Data.message
        })
      this.gtag_report_conversion()
    },
    gtag_report_conversion(url) {
      var callback = function () {
        if (typeof url != 'undefined') {
          window.location = url
        }
      }
      gtag('event', 'conversion', {
        send_to: 'AW-10826966991/Uh5DCIjnj4wDEM_P2aoo',
        event_callback: callback,
      })
      return false
    },
    addCounterOffer(form) {
      this.respondOffer('counterOffer', form)
      this.$emit('onSubmit')
    },
    rejectOffer(form) {
      this.respondOffer('reject', form)
      this.$emit('onSubmit')
    },
  },
  computed: {
    ...mapState(['respondActionLoading']),
    counterOfferState() {
      return Boolean(this.status && this.status == 2)
    },
    rejectOfferState() {
      return Boolean(this.status == 0)
    },
    acceptOfferState() {
      return Boolean(this.status && this.status == 1)
    },
  },
}
</script>
