<template>
  <b-overlay :show="loading" class="container chat-panel px-0" ref="chatPanel">
    <div class="row side-parent card-shadow">
      <FriendsList
        :allFriends="allFriends"
        :activeFriends="activeFriends"
        :recentSender="recentSender"
        @messagesChanged="changeMessagesInChatBox"
        @gettingMessages="gettingMessages"
      />

      <!-- start right side -->
      <ChatBox
        :key="activeChat ? activeChat.session_id : null"
        :allFriends="allFriends"
        :recentMessage="recentMessage"
        :messages="messages"
        :dataLoading="messagesLoading"
      />
    </div>
  </b-overlay>
</template>

<script>
import FriendsList from '~/components/dashboard/messaging/FriendsList'
import ChatBox from '~/components/dashboard/messaging/ChatBox'
import $axios from '~/helpers/axios-port'

export default {
  components: {
    FriendsList,
    ChatBox,
  },
  data() {
    return {
      activeFriends: [],
      recentMessage: null,
      messages: [],
      messagesLoading: false,
      allFriends: [],
    }
  },
  head(){
    return {
      title: this.dir === "ltr" ? "Messaging" : "الرسائل",
      meta: [
        // hid is used as unique identifier. Do not use `vmid` for it as it will not work
        {
          hid: 'description',
          name: 'description',
          content: this.dir === "ltr" ? "Messaging" : "الرسائل",
        }
      ]
    }
  },
  mounted() {
    var self = this
    self.getAllFriends()
    this.$echo
      .private(`App.Models.User.Sessions.${this.userID}`)
      .listen('NewMessageEvent', (e) => {
        var objIndex = self.allFriends.findIndex(
          (obj) => obj.id === e.sender_id
        )
        self.allFriends[objIndex].unreadCount = e.unreadCount
        self.allFriends[objIndex].session.unreadCount = e.unreadCount

        this.fetchRecentMessageSender(e)
      })
  },
  fetch({ store: { dispatch } }) {
    return dispatch('MessagingModule/loadFriendsData')
  },
  computed: {
    userID() {
      return this.$store.state.UserModule.user.user_id
    },
    activeChat() {
      return this.$store.state.MessagingModule.activeChat
    },
    recentSender() {
      return this.$store.state.MessagingModule.recentSender
    },
    loading() {
      return this.$store.state.MessagingModule.loading
    },
  },
  watch: {
    activeChat: function (val) {
      if (!val) return
      // this.$echo.leave(`Chat.${this.activeChat.session_id}`)
      setTimeout(() => {
        this.listenToMessages()
      })
    },
  },
  methods: {
    listenToMessages() {
      if (!this.activeChat) return
      this.$echo
        .private(`App.Models.User.Sessions.${this.userID}`)
        .listen('NewMessageEvent', (e) => {
          this.recentMessage = e
        })
    },
    fetchRecentMessageSender(obj) {
      this.$store.dispatch('MessagingModule/loadFriendsData', {
        params: '?page=1&per_page=1',
        recentSender: true,
        recentSenderObj: obj,
      })
    },
    broadcasting() {
      this.$echo
        .private(`Notify.${this.userID}`)
        .listen('NewMessageEvent', (e) => {
          this.fetchRecentMessageSender(e)
        })

      this.$echo
        .join('Chat')
        .here((users) => {
          this.activeFriends = [...users]
        })
        .joining((user) => {
          this.activeFriends = [...this.activeFriends, user]
        })
        .leaving((user) => {
          this.activeFriends = this.activeFriends.filter(
            (e) => e.id !== user.id
          )
        })
    },
    changeMessagesInChatBox: function (messages) {
      this.messages = messages
    },
    gettingMessages: function () {
      this.messagesLoading = true
    },
    getAllFriends() {
      var self = this
      $axios.$get(`/chat/getChatFriends?page=1&per_page=50`).then((res) => {
        self.allFriends = res.Data.data
      })
    },
  },
}
</script>

<style lang="scss" scoped>
.left-side {
  border-right: 2px solid $grayBackgroundColor;
}
.side-parent {
  .b-overlay-wrap {
    padding: 15px;
    &.left-side {
      @media screen and (min-width: 667px) {
        padding: 15px 0 0 15px;
      }
    }
  }
}
.chat-panel {
  margin: 30px auto;
  border-radius: 5px;
  border: 1px solid $grayBackgroundColor;
}
.left-side {
  input[type='text'] {
    background-color: $grayBackgroundColor;
    background-image: url('@/assets/images/message/searching.png');
    background-size: 15px;
    background-repeat: no-repeat;
    background-position: 7px 17px;
    border: 0;
    height: 45px;
    padding-left: 30px;
    margin-bottom: 15px;
    &:focus {
      box-shadow: none;
    }
  }
}
::v-deep() .vs__selected-options input {
  height: 32px;
}

.user-heading::v-deep() {
  .message-head {
    display: flex;
    justify-content: space-between;
    p {
      font-size: 12px;
      columns: $secondaryTextColor;
      text-transform: capitalize;
      margin-bottom: 3px;
      max-width: 100%;
    }
  }
  .message-shortcut {
    p {
      font-size: 14px;
      text-transform: capitalize;
    }
  }
}
// start right side style
.user-heading::v-deep() {
  // height: 60px;
  border-bottom: 1px solid $grayBackgroundColor;
}

.action-container {
  display: flex;
  justify-content: space-around;
  ::v-deep() button {
    width: 100px;
    height: 40px;
    line-height: 40px;
    padding: 0;
    text-align: center;
    font-size: 14px;
    text-transform: capitalize;
  }
}
[dir='rtl'] {
  .send-message .input-container .message {
    border-left: 0;
    border-right: 1px solid #cccbcb;
  }
  .message-list .list .notification-message {
    right: auto;
    left: 0;
  }
  .reading-message .message-sent {
    float: left;
    span {
      right: 0;
      left: auto;
      transform: translateX(110%);
    }
  }
  .reading-message .message-recived {
    float: right;
    span {
      right: auto;
      left: 0;
      transform: translateX(-110%);
    }
  }
  .pl-0 {
    padding-left: 15px !important;
    padding-right: 0 !important;
  }
}
@include media-breakpoint-down(md) {
  .reading-message .message {
    max-width: 95%;
  }
  .reading-message .message-recived span {
    left: 0;
    right: auto;
    transform: translate(0, 22px);
    bottom: 0;
  }
  .reading-message .message-sent span {
    left: auto;
    right: 0;
    transform: translate(0, 22px);
    bottom: 0;
  }
}
@include media-breakpoint-down(md) {
  [dir='rtl'] .reading-message .message-recived span {
    left: auto;
    right: 0;
    transform: translate(0, 22px);
    bottom: 0;
  }
  [dir='rtl'] .reading-message .message-sent {
    margin-left: 10px;
  }
  [dir='rtl'] .reading-message .message-sent span {
    left: 0;
    right: auto;
    transform: translate(0, 22px);
    bottom: 0;
  }
}
@include media-breakpoint-down(sm) {
  .chat-panel {
    border: 0;
  }
  .user-heading .user-image,
  .list .user-image {
    display: flex;
    align-items: center;
    justify-content: center;
    padding-top: 0;
  }
  .user-heading .user-image img,
  .list .user-image img {
    max-width: 50px;
    position: relative;
  }
  .message-list .list .notification-message {
    right: 15px;
  }
  .action-container button {
    width: 90px;
    font-size: 11px;
  }
  .side-parent {
    position: relative;
    margin: 0 10px !important;
    .left-side,
    .right-side {
      overflow: hidden;
      position: absolute;
      transition: all 0.4s ease-in-out;
      background: #fff;
      padding: 10px 0px;
      border-radius: 10px;
    }
    .left-side {
      width: 100%;
      border: 1px solid $grayBackgroundColor;
      z-index: 9;
    }
    .right-side {
      width: 100vw;
      padding: 15px;
      border: 1px solid #f4f4f4;
      margin: 10px 0;
    }
  }
  .active-chat-window {
    .side-parent {
      .left-side {
        width: 0;
        border: 0;
        z-index: 0;
      }
      .right-side {
        width: 100%;
        border: 1px solid $grayBackgroundColor;
        z-index: 9;
      }
    }
  }
}
@include media-breakpoint-down(sm) {
  [dir='rtl'] .message-list .list .notification-message {
    right: auto;
    left: 15px;
  }
}
</style>
