<template>
  <b-overlay :show="loadingProperty">
    <div class="side-bar-gallery" v-if="singleProperty">
      <PropertyGallery
        :images="singleProperty.property.property_image"
        v-if="
          singleProperty.property.property_image &&
          singleProperty.property.property_image.length
        "
      />
      <hr />
      <PropertyDetails
        :name="singleProperty.property.property_name"
        :address="singleProperty.property.property_state"
        :number-of-bathroom="singleProperty.property.no_bathrooms"
        :number-of-bedroom="singleProperty.property.no_of_bedroom"
        :area="singleProperty.property.property_area"
      />
      <PropertyDescription :description="singleProperty.property.description" />
      <PropertySleepingArrangements
        :bedrooms="getUnemptyBedrooms"
        v-if="getUnemptyBedrooms.length > 0"
      />
      <PropertyAmenities
        :amenities-categories="singleProperty.property.amenities"
        :messaging="true"
      />
      <span
        v-if="
          singleProperty.ratings && singleProperty.ratings.PropertyReviewData
        "
      >
        <h4 class="mb-20 review-title">{{ $t('property.reviews') }}</h4>

        <StarRateBox
          :with-star-rating="true"
          :rate="singleProperty.ratings.PropertyReviewData.avgTotal"
          :reviews-count="singleProperty.ratings.PropertyReviewData.totalCount"
          class="star-rate-box"
        />
      </span>
    </div>
  </b-overlay>
</template>

<script>
import PropertyGallery from '@/components/property-details/PropertyGallery.vue'
import PropertyDetails from '@/components/property-details/PropertyDetails'
import PropertyDescription from '@/components/property-details/PropertyDescription'
import PropertySleepingArrangements from '@/components/property-details/PropertySleepingArrangements'
import PropertyAmenities from '@/components/property-details/PropertyAmenities'
import StarRating from '@/components/shared/StarRating.vue'
import StarRateBox from '@/components/shared/StarRateBox'

export default {
  props: {
    singleProperty: {
      type: Object,
      required: false,
      default: () => ({}),
    },
    loadingProperty: {
      type: Boolean,
      required: false,
      default: false,
    },
  },
  components: {
    PropertyGallery,
    PropertyDescription,
    PropertySleepingArrangements,
    PropertyAmenities,
    StarRating,
    PropertyDetails,
    StarRateBox,
  },
  data() {
    return {}
  },
  computed: {
    getUnemptyBedrooms() {
      return (
        (this.singleProperty.property.bed_types || {}).bedrooms || []
      ).filter((currentBedroom) => currentBedroom.types.length > 0)
    },
  },
}
</script>

<style lang="scss" scoped>
.side-bar-gallery {
  ::v-deep() .swiper.gallery-top {
    height: 200px;
  }
}
.review-title {
  font-size: 22px;
  font-weight: bold;
  margin-bottom: 20px;
  text-transform: capitalize;
}
</style>
