<template>
  <b-overlay
    class="col-lg-4 col-md-5 left-side"
    v-if="allFriends && allFriends.length"
  >
    <div class="contact-search">
      <div class="px-3">
        <v-select
          v-if="properties && properties.length"
          v-model="selectedProperty"
          :options="properties"
          :reduce="(property) => property.property_id"
          @input="filterFriendsData"
          label="property_name"
          placeholder="Select Property"
        ></v-select>
      </div>
      <div class="friend-list message-list" @scroll="infiniteScrollHandler">
        <FriendCard
          v-for="(friend, index) in allFriends"
          :key="index"
          :friend="friend"
          @click="fetchMessagesHandler(friend)"
          :class="{
            'active-chat':
              activeChat &&
              friend.property_id == activeChat.property_id &&
              friend.session.id == activeChat.session_id,
            'active-friend': !!activeFriends.filter(
              (ele) => ele.id === friend.id
            ).length,
          }"
        />
        <aside class="w-100 py-3 text-center" v-if="dataLoading">
          <b-spinner label="Spinning"></b-spinner>
        </aside>
      </div>
    </div>
  </b-overlay>
</template>

<script>
import FriendCard from './FriendCard'
import $axios from '~/helpers/axios-port'

export default {
  props: {
    activeFriends: {
      type: Array,
      required: false,
    },
    allFriends: {
      type: Array,
      required: false,
      default: null,
    },
    recentSender: {
      type: Object,
      required: false,
      default: () => ({}),
    },
  },
  components: {
    FriendCard,
  },
  data() {
    return {
      contactSearch: '',
      selectedProperty: '',
      timeout: 300,
      page: 2,
      per_page: 10,
      dataComplete: false,
      dataLoading: false,
    }
  },
  watch: {
    selectedProperty: function (val) {
      this.page = 1
    },
  },
  mounted() {
    // get url parameter
    const urlParams = new URLSearchParams(window.location.search)
    const friend_id = urlParams.get('friend_id') || null
    this.fetchSingleFriend(friend_id)
  },
  computed: {
    friendsMeta() {
      return this.$store.state.MessagingModule.friendsMeta
    },
    activeChat() {
      return this.$store.state.MessagingModule.activeChat
    },
    loadingFriends() {
      return this.$store.state.MessagingModule.loadingFriends
    },
    properties() {
      return this.$store.state.MessagingModule.friendsProperties
    },
  },

  methods: {
    infiniteScrollHandler(el) {
      if (this.dataComplete || !this.friendsMeta) return
      if (
        el.srcElement.offsetHeight + el.srcElement.scrollTop <
        el.srcElement.scrollHeight
      )
        return

      this.dataLoading = true
      this.filterFriendsData()
        .then((res) => {
          this.page++
          if (
            this.allFriends &&
            this.friendsMeta &&
            (this.allFriends.length === this.friendsMeta.total ||
              this.page > this.friendsMeta.pages_count)
          ) {
            this.dataComplete = true
          }
        })
        .finally(() => {
          this.dataLoading = false
        })
    },
    fetchSingleFriend(id) {
      if (!id) return
      this.$store.dispatch('MessagingModule/fetchSingleFriend', { id })
    },
    fetchMessagesHandler(friend) {
      this.$emit('gettingMessages')
      if (this.activeChat) {
        this.$echo.leave(`Chat.${this.activeChat.session_id}`)
      }
      this.$root.$emit('MessagingSidebarIsOpen', false)
      if (window.innerWidth <= 767) {
        document.querySelector('.right-side').classList.remove('d-none')
        document.querySelector('.right-side').classList.remove('d-xs-none')
        document.querySelector('.left-side').classList.add('d-none')
      }

      this.$store
        .dispatch('MessagingModule/activateChat', {
          session_id: friend?.session?.id,
          property_id: friend.property_id,
          friend_id: friend.id,
        })
        .then(() => {
          document.querySelector('.right-side').classList.remove('d-xs-none')
        })
      var self = this
      $axios
        .$get(
          `/chat/session/${this.activeChat?.session_id}/${this.activeChat?.property_id}/chats`
        )
        .then((res) => {
          var objIndex = self.allFriends.findIndex(
            (obj) => obj.id === self.activeChat.friend_id
          )
          // self.allFriends[objIndex].unreadCount = 0
          // self.allFriends[objIndex].session.unreadCount = 0
          this.$emit('messagesChanged', res.Data.data)
        })
        .catch((error) => {
          console.log(error)
          this.errored = true
        })
        .finally(() => console.log('finally'))
      // this.$store
      //   .dispatch('MessagingModule/loadMessages', {
      //     session_id: friend.session ? friend.session?.id : null,
      //     property_id: friend.property_id,
      //     friend_id: friend.id,
      //   })
      //   .then(() => {
      //     document.querySelector('.right-side').classList.remove('d-xs-none')
      //   })
      //   .finally(() => {})

      this.$store.dispatch('MessagingModule/readRecentMessage', {
        session_id: friend.session ? friend?.session?.id : null,
        property_id: friend.property_id,
        friend_id: friend.id,
      })
    },
    filterFriendsData(property) {
      return Promise.all([
        this.$store.dispatch('MessagingModule/loadFriendsData', {
          params: `?page=${this.page}&per_page=${this.per_page}&${
            property ? `filter[property]=${property}` : null
          }`,
          recentSender: false,
          filterProperty: !!property,
        }),
      ])
    },
  },
}
</script>

<style lang="scss" scoped>
.contact-search {
  height: 100%;
}
.message-list::v-deep() {
  margin-top: 20px;
  max-height: 410px;
  @media screen and (min-width: 767px) {
    max-height: 70vh;
  }
  overflow-y: auto;
  @media screen and (max-width: 600px) {
    max-height: 200px;
  }
  .list {
    position: relative;
    cursor: pointer;
    height: 92px;
    padding: 20px 15px;
    border: 1px solid $grayBackgroundColor;
    border-left: 0;
    border-right: 0;
    .notification-message {
      position: absolute;
      right: 10px;
      bottom: 10px;
      background: $mainColorLight;
      color: #fff;
      font-size: 11px;
      width: 22px;
      height: 22px;
      border-radius: 50%;
      text-align: center;
      line-height: 22px;
    }
  }
}

.active-chat {
  background-color: #f4f4f4;
}

.active-friend::v-deep() {
  .user-image {
    &::after {
      content: '';
      background-color: chartreuse;
      border-radius: 50%;
      width: 10px;
      height: 10px;
      position: absolute;
      right: 0;
      bottom: 0;
      z-index: 9;
    }
  }
}
.card-shadow {
  box-shadow: 0 2px 5px #ccc !important;
}
</style>
