<template>
  <div class="message-side-bar">
    <!-- TODO : add transition  to closing and opening -->
    <b-sidebar
      id="messaging-sidebar"
      class=""
      :right="!isRTL"
      v-model="isVisible"
    >
      <!-- :show="loadingProperty || loadingOffer" -->
      <b-overlay :opacity="1">
        <div class="px-3 py-2" v-if="friend">
          <PropertySide
            v-if="friend.isLandlord === friend.id"
            :singleProperty="singleProperty"
            :loadingProperty="loadingProperty"
          />
          <ProfilePreviewColumn
            :profileData="singleOffer"
            :offer_id="friend.offer_id"
            :loadingOffer="loadingOffer"
            v-if="friend.isLandlord !== friend.id"
          />
        </div>
      </b-overlay>
    </b-sidebar>
  </div>
</template>

<script>
import PropertySide from './PropertySide'
import ProfilePreviewColumn from '../profile/ProfilePreviewColumn'

export default {
  name: 'messageSideBar',
  components: {
    ProfilePreviewColumn,
    PropertySide,
  },
  props: {
    friend: {
      type: Object,
      required: false,
    },
  },
  data() {
    return {
      isVisible: false,
    }
  },
  mounted() {
    this.$root.$on('DashboardSidebarIsOpen', (val) => {
      if (!val) return
      this.isVisible = false
    })
    this.$root.$on('MessagingSidebarIsOpen', (val) => {
      this.isVisible = val
    })
  },
  watch: {
    isVisible(newVal) {
      this.$store.dispatch(
        'MessagingModule/getSingleProperty',
        this.friend?.property_slug
      )
      this.$store.dispatch(
        'MessagingModule/getSingleOffer',
        this.friend?.offer_id
      )
      this.$root.$emit('MessagingSidebarIsOpen', newVal)
    },
  },
  computed: {
    singleProperty() {
      return this.$store.state.MessagingModule.singleProperty
    },
    loadingProperty() {
      return this.$store.state.MessagingModule.loadingProperty
    },
    singleOffer() {
      return this.$store.state.MessagingModule.singleOffer
    },
    loadingOffer() {
      return this.$store.state.MessagingModule.loadingOffer
    },
    isRTL() {
      return this.$store.getters['CoreModule/isRTL']
    },
  },
}
</script>

<style lang="scss" scoped>
::v-deep() #messaging-sidebar {
  /* @media screen and (min-width: 767px) {
    transform: translateX(400px);
  } */
  min-height: 100vh;
}
.message-side-bar {
  position: absolute;
  top: 0;
  .toggle-button {
    max-width: 45px;
    height: 45px;
    opacity: 0;
  }
}

::v-deep() .b-sidebar {
  width: 400px;
  @media (max-width: 400px) {
    width: 100vw;
  }
}

::v-deep() .b-sidebar-body {
  &::-webkit-scrollbar {
    width: 10px;
  }
  &::-webkit-scrollbar-track {
    background: #fff;
  }
  &::-webkit-scrollbar-thumb {
    background: #fff;
  }
}
</style>
