<template>
  <div class="gallery-container">
    <swiper
      :dir="$i18n.locale === 'en' ? 'ltr' : 'rtl'"
      class="swiper gallery-top"
      :options="swiperOptionTop"
      ref="swiperTop"
    >
      <swiper-slide v-for="(image, index) in images" :key="`image-${index}`">
        <AppImage :src="image" :quality="90" width="700" height="700" />
      </swiper-slide>
      <div
        class="swiper-pagination d-md-none"
        slot="pagination"
        v-if="(images || []).length > 1"
      ></div>
    </swiper>

    <swiper
      :dir="$i18n.locale === 'en' ? 'ltr' : 'rtl'"
      v-if="images && images.length > 1"
      class="swiper gallery-thumbs d-none d-md-block"
      :options="swiperOptionThumbs"
      ref="swiperThumbs"
    >
      <swiper-slide v-for="(image, index) in images" :key="`image-${index}`">
        <AppImage :src="image" width="200" height="200" />
      </swiper-slide>
      <div
        class="swiper-nav-button swiper-button-next swiper-button-white"
        slot="button-next"
      ></div>
      <div
        class="swiper-nav-button swiper-button-prev swiper-button-white"
        slot="button-prev"
      ></div>
    </swiper>
  </div>
</template>

<script>
import { Swiper, SwiperSlide } from 'vue-awesome-swiper'

export default {
  components: {
    Swiper,
    SwiperSlide,
  },
  props: {
    images: {
      type: Array,
      required: true,
    },
  },
  data() {
    return {
      swiperOptionTop: {
        slidesPerView: 1,
        loopedSlides: 0, // looped slides should be the same
        spaceBetween: 0,
        breakpoints: {
          320: {
            pagination: {
              el: '.swiper-pagination',
              clickable: true,
              bulletActiveClass: 'swiper-pagination-bullet-active',
              bulletClass: 'swiper-pagination-bullet',
            },
          },
        },
      },
      swiperOptionThumbs: {
        slidesPerView: 5,
        slidesPerView: 'auto',
        loopedSlides: 0, // looped slides should be the same
        spaceBetween: 10,
        centeredSlides: true,
        touchRatio: 0.2,
        slideToClickedSlide: true,
        breakpoints: {
          1440: {
            slidesPerView: 3,
            spaceBetween: 30,
          },
          1280: {
            slidesPerView: 3,
            spaceBetween: 30,
          },
          991: {
            slidesPerView: 3,
            spaceBetween: 50,
          },
          768: {
            slidesPerView: 2,
            spaceBetween: 30,
          },
          640: {
            slidesPerView: 2,
            spaceBetween: 30,
          },
          320: {
            slidesPerView: 1,
            spaceBetween: 10,
          },
        },
        navigation: {
          nextEl: '.swiper-button-next',
          prevEl: '.swiper-button-prev',
        },
      },
    }
  },
  mounted() {
    this.$nextTick(() => {
      const swiperTop = (this.$refs.swiperTop || {}).$swiper
      const swiperThumbs = (this.$refs.swiperThumbs || {}).$swiper
      if (swiperTop && swiperThumbs) {
        swiperTop.controller.control = swiperThumbs
        swiperThumbs.controller.control = swiperTop
      }
    })
  },
}
</script>

<style lang="scss" scoped>
.gallery-container {
  .swiper {
    img {
      /* width: 100%; */
      /* max-height: 100%; */
      margin: auto;
      display: block;
      border-radius: 5px;
    }
    &.gallery-top {
      width: 100%;
      height: 350px;

      ::v-deep() {
        .swiper-slide {
          img {
            position: relative;
            top: 50%;
            transform: translateY(-50%);
          }
        }
      }
    }
    &.gallery-thumbs {
      height: 112px;
      box-sizing: border-box;
      padding: 15px 0;
      ::v-deep() {
        .swiper-slide {
          width: 112px;
          height: 77px;
          opacity: 0.3;
          overflow: hidden;
        }
        .swiper-slide-active {
          opacity: 1;
        }
        .swiper-wrapper {
          justify-content: center;
          /* left: -40%; */
        }
      }
    }
    .swiper-nav-button {
      width: 30px;
      height: 30px;
      display: flex;
      justify-content: center;
      align-items: center;
      border-radius: 50%;

      color: #000;
      background-color: #fff;
      box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.47);
      font-style: 12px;
      &:after {
        font-size: 14px;
        font-weight: 700;
      }
    }
  }
  ::v-deep() .swiper-pagination-bullet {
    background: $grayLightColor;
    border: solid 1px $secondaryTextColor;
    width: 9px;
    height: 9px;
  }
  ::v-deep() .swiper-pagination-bullet-active {
    background: #fff;
  }
  @include media-breakpoint-down(sm) {
    .swiper {
      &.gallery-top {
        height: 200px;
      }
    }
  }
}
</style>
