<template>
  <div class="send-message card-shadow">
    <div class="container">
      <b-tooltip v-if="disabled" target="disabled-form" variant="primary">{{
        $t('message.offerMustBeAcceptedToActivateMessages')
      }}</b-tooltip>
      <b-form
        class="row"
        @submit.prevent="sendMessageHandler"
        id="disabled-form"
      >
        <div class="col-9 col-sm-10 pl-0">
          <div class="input-container">
            <div class="message">
              <b-form-input
                v-model="newMessage"
                :placeholder="$t('message.typeAmessage')"
                :disabled="sendingMessageLoader || disabled"
              ></b-form-input>
            </div>
          </div>
        </div>
        <div class="col-3 col-sm-2">
          <div class="row">
            <div class="col-12 px-2">
              <AppButton
                class="send-btn"
                variant="primary"
                :loading="sendingMessageLoader"
                :disabled="disabled"
                type="submit"
              >
                {{ $t('message.send') }}
              </AppButton>
            </div>
          </div>
        </div>
      </b-form>
    </div>
  </div>
</template>

<script>
import API from '~/store/MessagingModule/api'

export default {
  props: {
    disabled: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      newMessage: null,
    }
  },
  computed: {
    activeChat() {
      return this.$store.state.MessagingModule.activeChat
    },
    sendingMessageLoader() {
      return this.$store.state.MessagingModule.sendingMessageLoader
    },
  },
  mounted() {},
  methods: {
    sendMessageHandler() {
      const messageObj = {
          content: this.newMessage,
          property: this.activeChat?.property_id,
          to_user: this.activeChat?.friend_id,
        },
        session_id = this.activeChat?.session_id

      this.$emit('sendingMessage', this.newMessage)
      this.newMessage = null
      API.sendMessage(messageObj, session_id)
        .then((res) => {
          this.$emit('sentMessage')
        })
        .finally(() => {})
    },
  },
}
</script>

<style lang="scss" scoped>
.send-message {
  height: 65px;
  display: flex;
  align-items: center;
  padding: 0 5px;
  .input-container {
    background: $grayBackgroundColor;
    height: 45px;
    border-radius: 5px;
    .file {
      display: inline-block;
      width: 50px;
      overflow: hidden;
      background-image: url('@/assets/images/message/file.png');
      background-size: 25px;
      background-repeat: no-repeat;
      background-position: 13px 3px;
      transform: translateY(5px);
      input[type='file'] {
        opacity: 0;
      }
    }
    .message {
      display: inline-block;
      width: calc(100% - 65px);
      border-left: 1px solid #cccbcb;
      transform: translateY(-2px);
      padding: 0 5px;
      input {
        background: transparent;
        border: 0;
        padding: 0;
        &:focus {
          box-shadow: none;
        }
        &::placeholder {
          font-size: 14px;
        }
      }
    }
  }
  ::v-deep() .send-btn {
    width: 100%;
    padding: 10px 0px;
    font-size: 14px;
    text-align: tra;
    text-transform: capitalize;
  }
  .record {
    display: block;
    margin: 6px auto 0;
    width: 25px;
  }
}
</style>
