<template>
  <div class="list" v-on="$listeners" v-if="friend">
    <div class="container">
      <slot></slot>
      <div class="row">
        <div class="col-2 col-sm-1 col-md-2 px-0">
          <div class="user-image" :class="{ 'friend-online': friend.online }">
            <AppImage
              v-if="friend.user_profile && friend.user_profile !== ''"
              :src="
                friend.id === friend.isLandlord
                  ? friend.property_image
                  : friend.user_profile
              "
              :alt="`the profile photo of ${friend.user_name}`"
              :width="50"
              :height="50"
              :quality="30"
              class="w-100 h-100"
            />
            <img
              v-else
              src="@/assets/images/message/profile.png"
              :alt="`the profile photo of ${friend.user_name}`"
              width="50"
              height="50"
            />
          </div>
        </div>
        <div class="col-10 col-sm-11 col-md-10">
          <div class="message-head">
            <p class="name" v-if="friend.user_name">
              {{ friend.user_name }}
            </p>
            <p class="date" v-if="friend.last_message_dateTime">
              {{ formatDate(friend.last_message_dateTime) }}
            </p>
          </div>
          <p class="location font-14">
            {{
              friend.id === friend.isLandlord
                ? friend.property_location
                : friend.property_name | excerpt
            }}
          </p>
          <p class="name mb-0">
            {{ friend.property_type }}
          </p>
        </div>
      </div>
    </div>
    <div
      class="notification-message"
      v-if="friend && friend.session && friend.session.unreadCount"
    >
      {{ friend.session.unreadCount }}
    </div>
  </div>
</template>

<script>
export default {
  props: {
    friend: {
      type: Object,
      required: true,
    },
  },
  data() {
    return {}
  },
  filters: {
    excerpt: function (value) {
      if (!value) return ''
      return value.toString().slice(0, 25) + ' ' + ' ...'
    },
  },
  methods: {
    formatDate(d) {
      const date = new Date(d)
      // if date is today return time only
      if (date.toDateString() === new Date().toDateString()) {
        return this.getTimeFromDate(date) + this.suffixAmPm(date)
      }

      // if date is yesterday return yesterday
      if (
        date.toDateString() ===
        new Date(new Date().getTime() - 86400000).toDateString()
      ) {
        return 'Yesterday'
      }

      // if date is in the past return month day year
      if (date.getTime() < new Date().getTime()) {
        return (
          date.getDate() +
          '/' +
          (date.getMonth() + 1) +
          '/' +
          date.getFullYear()
        )
      }
    },
    getTimeFromDate(date) {
      return (
        (date.getHours() > 12 ? date.getHours() - 12 : date.getHours()) +
        ':' +
        date.getMinutes()
      )
    },
    suffixAmPm(date) {
      return date.getHours() >= 12 ? ' PM' : ' AM'
    },
  },
}
</script>

<style lang="scss" scoped>
.user-image {
  position: relative;

  width: 50px;
  height: 50px;
  img {
    border-radius: 50%;
    position: absolute;
    top: 0;
    left: 0;
  }
}

.list {
  .message-head {
    display: flex;
    justify-content: space-between;
    p {
      font-size: 12px;
      columns: $secondaryTextColor;
      text-transform: capitalize;
      margin-bottom: 3px;
      max-width: 100%;
    }
  }
  .message-shortcut {
    p {
      font-size: 14px;
      text-transform: capitalize;
    }
  }
}

.font-14 {
  font-size: 14px;
}
.location {
  margin-bottom: 5px;
  color: $mainTextColor;
}
.name {
  font-size: 12px;
  color: $secondaryTextColor;
  margin-bottom: 8px;
}

.date {
  color: $secondaryTextColor;
  font-size: 12px;
}
</style>
