<template>
  <div
    class="amenity-option-container"
    :class="{ 'custom-control custom-checkbox': hasCheckbox }"
  >
    <input
      type="checkbox"
      name="checkbox-amenity"
      :value="amenity"
      class="custom-control-input checkbox-amenity"
      v-model="checkedValue"
      :id="amenity.id"
      v-if="hasCheckbox"
    />

    <label :class="{ 'custom-control-label': hasCheckbox }" :for="amenity.id">
      <div class="d-flex amenity-option-subcontainer">
        <!-- <img :src="amenity.icon" :alt="amenity.amenity" /> -->
        <AppImage
          withoutCompression
          :src="amenity.icon"
        />

        <p>
          {{ amenity.amenity }}
        </p>
      </div>
    </label>
  </div>
</template>
<style lang="scss" scoped>
.amenity-option-container {
  margin-bottom: 40px;
  [dir='rtl'] &.custom-checkbox {
    padding-right: 1.5rem;
    padding-left: 0;
  }
  .custom-control-label::before,
  .custom-control-label::after {
    [dir='rtl'] & {
      right: -1.5rem;
    }
  }
  .amenity-option-subcontainer {
    padding: 8px 12px;
    background: linear-gradient(0deg, rgba(38, 45, 61, 0.04), rgba(38, 45, 61, 0.04)), #FFFFFF;
    border-radius: 8px;
    [dir='ltr'] & {
      direction: ltr;
    }
    img {
      width: 28px;
      height: 28px;
    }
    p {
      font-weight: 400;
      font-size: 12px;
      line-height: 14px;
      color: #5E696F;
      margin: 4.9px 0 0.6px 11.1px;
      [dir='rtl'] & {
        margin: 0.9px 11.1px 0.6px 0;
      }
    }
  }
}
</style>
<script>
export default {
  name: 'AmenityOption',
  props: {
    amenity: {
      type: Object,
      required: true,
    },
    value: {
      // this prop for v-model
      type: Array,
      required: false,
    },
    hasCheckbox: {
      type: Boolean,
      required: false,
      default: false,
    },
  },
  methods: {
    emitInputEvent(value) {
      this.$emit('input', value)
    },
  },
  computed: {
    checkedValue: {
      get() {
        return this.value
      },
      set(value) {
        this.emitInputEvent(value)
      },
    },
  },
}
</script>
