<template>
  <div>
    <b-modal
      ref="counter-offer-modal"
      v-bind="$attrs"
      centered
      hide-header
      hide-footer
      v-model="showCounterModel"
      @hidden="$emit('update:showCounterModel')"
    >
      <div v-if="errorMessage">
        <b-alert show variant="danger">
          <ul class="mb-0 p-0">
            <li style="text-align: center">
              {{ errorMessage }}
            </li>
          </ul>
        </b-alert>
      </div>
      <ValidationObserver v-slot="{ handleSubmit }">
        <b-form @submit.prevent="handleSubmit(addCounterOffer)">
          <b-overlay
            :show="
              respondActionLoading.type == 'counterOffer' &&
              respondActionLoading.loading == true
            "
          >
            <header class="d-flex align-items-center">
              <h2 class="title-modal">{{ $t('offer.counterOffer') }}</h2>
            </header>

            <b-row>
              <b-col cols="6">
                <p class="text-modal">{{ $t('offer.offerFromTenant') }} :</p>
              </b-col>
              <b-col cols="6">
                <p class="text-modal">
                  {{ tenantOfferPrice }} {{ $t('common.egp') }} /
                  {{ offerTerm }}
                </p>
              </b-col>
              <b-col cols="12">
                <span class="text-modal">{{
                  $t('offer.yourCounterOffer')
                }}</span>
                <p class="small-text-modal">
                  {{ $t('offer.enterCounterOffer') }}
                </p>

                <b-row>
                  <b-col cols="8">
                    <app-input
                      v-model.number="form.offer_price"
                      validation-name="Offer Price"
                      validation-rules="required|number|min_value:1"
                      input-id="offer_price"
                      type="number"
                    ></app-input>
                  </b-col>
                  <b-col cols="4" class="p-0">
                    <p class="text-modal static-text">
                      {{ $t('common.egp') }} / {{ offerTerm }}
                    </p>
                  </b-col>
                </b-row>
                <div class="button-container">
                  <app-button
                    type="submit"
                    :label="$t('offer.submitOffer')"
                    variant="primary"
                    class="submit-offer"
                  />
                </div>
              </b-col>
            </b-row>
          </b-overlay>
        </b-form>
      </ValidationObserver>
    </b-modal>
  </div>
</template>
<style lang="scss" scoped>
.title-modal {
  font-size: 18px;
  font-weight: bold;
  margin-bottom: 24px;
}
.text-modal {
  font-size: 14px;
}
.small-text-modal {
  font-size: 12px;
  color: $secondaryTextColor;
}
::v-deep() .modal-dialog {
  max-width: 330px;
  margin: auto;
}
::v-deep() input::-webkit-outer-spin-button,
::v-deep() input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
::v-deep() #offer_price {
  height: 45px;
}
.static-text {
  margin-top: 10px;
}
::v-deep() .custom-checkbox {
  margin-bottom: 12px;
}
.button-container {
  display: flex;
  justify-content: flex-end;
  .submit-offer {
    margin-top: 25px;
  }
}
</style>
<script>
import { createNamespacedHelpers } from 'vuex'
const { mapState } = createNamespacedHelpers('PropertyModule')
export default {
  name: 'CounterOfferModal',
  props: {
    tenantOfferPrice: {
      type: Number,
      required: true,
    },
    offerPriceTerm: {
      type: Number | String,
      required: true,
    },
    errorMessage: {
      type: String,
      default: '',
    },
    showCounterModel: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      form: {
        offer_price: null,
        notes: '',
      },
      notesStatus: 0,
      modelVisibility: false,
    }
  },
  methods: {
    addCounterOffer() {
      this.$emit('counterOffer', this.form)
    },
    resetModal() {
      this.form.offer_price = null
      this.form.notes = ''
      this.$refs['counter-offer-modal'].hide()
    },
  },
  computed: {
    ...mapState(['respondActionLoading']),
    ...mapState({
      visibility: (state) => state.respondActionModalVisibility.visibility,
      respondType: (state) => state.respondActionModalVisibility.type,
    }),

    offerTerm() {
      if (this.offerPriceTerm == 1) {
        return this.$t('common.month')
      } else {
        return this.$t('common.night')
      }
    },
  },
  watch: {
    showCounterModel: {
      handler(value) {
        if (!value) {
          this.$emit('reset-model')
        }
      },
      immediate: true,
    },
  },
}
</script>
