            list.offer_price_term == 1 ? $t('common.month') : $t('common.night')
          }}
        </span>
      </li>
      <li
        v-if="list.creation_date"
        class="d-flex align-items-center justify-content-between flex-nowrap"
      >
        <span> {{ $t('offer.offeredAt') }} </span>
