  color: $mainTextColor;
  margin-bottom: 16px;
}
li {
  margin-bottom: 16px;
  span {
    font-size: 14px;
    color: $mainTextColor;
  }
}

.badge.badge-outlined.badge-success {
  height: 25px;
  padding: 5px 8px;
  border-radius: 3px;
  border: solid 1px #80c363;
  background-color: #ffffff;
  font-size: 12px;
  color: #80c363;
}
</style>
