<template>
  <PropertySection class="property-details-section">
    <template v-slot:title>
      {{ name }}
    </template>

    <template>
      <p class="property-address">
        {{ address }}
      </p>
      <div class="property-details-container">
        <b-list-group horizontal>
          <b-list-group-item>
            <svg-icon
              name="double-bed"
              viewBox="0 0 28 28"
              class="property-detail-icon"
            />
            <span class="property-detail-label"
              >{{ numberOfBedroom }}
              {{ $t('property.propertyDetails.bedrooms') }}</span
            >
          </b-list-group-item>
          <b-list-group-item>
            <svg-icon
              name="grey-bath"
              viewBox="0 0 16 16"
              class="property-detail-icon"
            />
            <span class="property-detail-label"
              >{{ numberOfBathroom }}
              {{ $t('property.propertyDetails.bathrooms') }}</span
            ></b-list-group-item
          >
          <b-list-group-item>
            <svg-icon
              name="grey-area"
              viewBox="0 0 16 16"
              class="property-detail-icon"
            />

            <span class="property-detail-label"
              >{{ area }}
              {{ $t('property.propertyDetails.squareMeters') }}</span
            ></b-list-group-item
          >
        </b-list-group>
      </div>
    </template>
  </PropertySection>
</template>
<style lang="scss" scoped>
.property-details-section {
  margin-top: 15px;
  @include media-breakpoint-down(sm) {
    padding-bottom: 20px;
    margin-bottom: 15px;
  }
}
.property-address {
  color: $secondaryTextColor;
  font-size: 14px;
}
.property-details-container {
  .property-detail-label {
    font-size: 14px;
    margin-left: 5px;
    [dir='rtl'] & {
      margin-left: 0;
      margin-right: 5px;
    }
  }
  svg {
    width: 24px;
    height: 24px;
  }
  ::v-deep() .list-group-item {
    padding: 0 24px 0 0;
    border: 0;

    [dir='rtl'] & {
      padding: 0 0 0 24px;
    }
  }
}
@include media-breakpoint-down(sm) {
  .property-address,
  .property-detail-label {
    font-size: 12px !important;
  }

  .property-details-container {
    svg {
      width: 20px !important;
      height: 20px !important;
    }
  }
  ::v-deep() .list-group-item {
    padding: 0 15px 0 0 !important;

    [dir='rtl'] & {
      padding: 0 0 0 15px !important;
    }
  }
}
</style>
<script>
import PropertySection from '@/components/property-details/PropertySection'
export default {
  name: 'PropertyDetails',
  components: {
    PropertySection,
  },
  props: {
    name: {
      type: String,
    },
    address: {
      type: String,
    },
    numberOfBathroom: {
      type: Number,
    },
    numberOfBedroom: {
      type: Number,
    },
    area: {
      type: Number,
    },
  },
}
</script>
