<template>
  <aside class="mx-auto" v-if="profileData && profileData.from_user">
    <figure-with-describtion
      :name="profileData.from_user.name"
      :image-url="profileData.from_user.image_url"
    />
    <Separator />
    <RespondActions
      :tenant-offer-price="profileData.offer_price"
      :offer-price-term="profileData.offer_price_term"
      :offer-id="selectedOfferId"
      :status="profileData.status"
      :rejection-reason="rejectionReason"
      @onSubmit="onSubmit"
    />

    <list-with-title :title="$t('common.offer')" :list="profileData" />

    <Separator v-if="profileData.from_user" />

    <list-profile
      v-if="profileData.from_user"
      :title="$t('common.profile')"
      :list="profileData"
    />
    <Separator
      v-if="profileData.from_user && profileData.from_user.education"
    />

    <list-education
      v-if="profileData.from_user && profileData.from_user.education"
      :title="$t('common.educationWork')"
      :list="profileData"
    />
    <Separator v-if="profileData.note" />

    <section v-if="profileData.note">
      <h3>{{ $t('common.notes') }}</h3>
      <p>
        {{ profileData.note }}
      </p>
    </section>
    <Separator v-if="reviews && reviews.length" />

    <section class="reviews" v-if="reviews && reviews.length">
      <h3>{{ $t('common.reviews') }}</h3>
      <b-overlay :show="tenantReviewLoading" rounded="sm" :opacity="1.0">
        <review
          v-for="(review, index) in reviews"
          :key="index"
          :review="review"
        />
      </b-overlay>
      <br />
      <b-button
        class="p-0 review-btn"
        @click="showMore"
        variant="link"
        v-if="!less"
      >
        <svg-icon name="blue-arrow-right" class="arrow-custom"></svg-icon>
        {{ $t('common.viewMore') }}
      </b-button>
      <b-button
        class="p-0 review-btn"
        @click="getReivews"
        variant="link"
        v-else
      >
        <svg-icon name="blue-arrow-right" class="arrow-custom"></svg-icon>
        {{ $t('common.viewLess') }}
      </b-button>
    </section>
  </aside>
</template>

<script>
import {
  FigureWithDescribtion,
  ListWithTitle,
  Review,
  ListEducation,
  ListProfile,
} from '~/components/shared/content/index'
import Separator from '~/components/shared/Separator'
import RespondActions from '@/components/shared/RespondActions'
import { createNamespacedHelpers } from 'vuex'
const { mapState, mapActions } = createNamespacedHelpers('PropertyModule')

export default {
  components: {
    FigureWithDescribtion,
    ListWithTitle,
    Review,
    Separator,
    ListEducation,
    ListProfile,
    RespondActions,
  },
  props: ['profileData', 'offer_id', 'loadingOffer', 'hideCloseBtn'],
  data() {
    return {
      reviews: [],
      less: false,
      tenantReviewLoading: false,
    }
  },
  mounted() {
    this.getReivews()
  },
  watch: {
    profileData() {
      this.getReivews()
    },
  },
  computed: {
    ...mapState(['offerDetails', 'selectedOfferId', 'rejectionReason']),
    isLandlord() {
      return this.$store.state.UserModule?.user?.isLandlord
    },
    userID() {
      return this.$store.state.UserModule?.user?.user_id
    },
  },
  methods: {
    ...mapActions(['getTenantReviewNotes']),
    getReivews() {
      if (!this.profileData || !this.profileData.from_user) return

      let id = this.profileData.from_user.id
      this.tenantReviewLoading = true
      this.getTenantReviewNotes({
        tenant_id: id,
        per_page: 2,
        offer_id: this.offer_id,
      })
        .then((res) => {
          this.reviews = res.Data.data
          this.less = false
        })
        .finally(() => {
          this.tenantReviewLoading = false
        })
    },
    onSubmit() {
      this.$emit('onSubmit')
    },
    showMore() {
      if (!this.profileData || !this.profileData.from_user) return
      let id = this.profileData.from_user.id
      this.tenantReviewLoading = true
      this.getTenantReviewNotes({
        tenant_id: id,
      })
        .then((res) => {
          this.reviews = res.Data.data
          this.less = true
        })
        .finally(() => {
          this.tenantReviewLoading = false
        })
    },
  },
}
</script>

<style lang="scss" scoped>
aside {
  .hidden-sm-and-down {
    @media only screen and (max-width: 1024px) {
      display: none;
    }
  }
  max-width: 380px;
  width: 100%;
  padding: 15px;
  .actions {
    margin-bottom: 24px;
  }

  h3 {
    font-size: 18px;
    font-weight: bold;
    color: $mainTextColor;
    margin-bottom: 10px;
  }

  p {
    font-size: 14px;
    color: $mainTextColor;
    line-height: 20px;
  }
}
::v-deep() button {
  font-size: 14px;
}

.reviews {
  font-size: 14px;
}
.review-btn {
  display: flex;
  align-items: center;
  color: $mainColorLight;
}
.arrow-custom {
  width: 24px;
  height: 24px;
  [dir='rtl'] & {
    transform: rotate(180deg);
  }
}
</style>
