<template>
  <div
    class="right-side position-relative d-xs-none"
    :class="
      !activeFriend && !allFriends && !allFriends.length
        ? 'col-12'
        : 'col-lg-8 col-md-7 '
    "
  >
    <b-overlay>
      <div
        class="user-heading cursor-pointer card-shadow"
        v-if="activeFriend"
        v-b-toggle="['messaging-sidebar']"
      >
        <div class="container">
          <div class="row">
            <!-- -->
            <div
              class="px-0 col-1 d-md-none d-flex align-items-center justify-content-center"
            >
              <b-button
                class="p-0 bg-transparent border-0"
                @click="displayList"
                aria-label="show friends list"
              >
                <svg-icon
                  :class="`${$i18n.locale === 'ar' && 'chevron-icon'}`"
                  name="bold-chevron-left"
                  width="20"
                  height="30"
                />
              </b-button>
            </div>
            <div class="col-3 col-sm-1 px-2 pt-1">
              <div class="user-image" id="user-image">
                <AppImage
                  v-if="activeFriend && activeFriend.user_profile"
                  :src="activeFriend.property_image"
                  alt="photo"
                />
                <img
                  v-else
                  src="@/assets/images/message/profile.png"
                  alt="photo"
                />
                <b-tooltip
                  target="user-image"
                  :title="$t('common.clickMoreDetails')"
                ></b-tooltip>
              </div>
            </div>
            <div class="col-8 col-sm-9 px-0">
              <div class="message-head">
                <p class="name mb-0">
                  {{ activeFriend.user_name }}
                </p>
                <!-- landlord location  -->
                <p
                  class="font-14 mb-0 location"
                  v-if="activeFriend.id === activeFriend.isLandlord"
                  :title="activeFriend.property_location"
                >
                  {{ activeFriend.property_location }}
                </p>
                <!-- tennant property name  -->
                <p
                  class="font-14 mb-0 location"
                  v-if="activeFriend.id !== activeFriend.isLandlord"
                  :title="activeFriend.property_name"
                >
                  {{ activeFriend.property_name | excerpt }}
                </p>
              </div>
              <p class="name mb-0">
                {{ activeFriend.property_type }}
              </p>
            </div>
          </div>
        </div>
      </div>
      <section
        class="reading-message m-0 p-0 card-shadow"
        ref="body"
        @scroll="infiniteScrollHandler"
      >
        <aside class="w-100 py-3 text-center" v-if="dataLoading">
          <b-spinner label="Spinning"></b-spinner>
        </aside>

        <transition name="popin" tag="div" v-if="activeFriend">
          <!-- 0 : sent , 1 : recieved  -->
          <div class="mt-4">
            <div v-for="(message, i) in allMessages" :key="i">
              <div
                :class="{
                  'message-container message-received': message.user_id !== userID,
                  'message-container message-sent': message.user_id === userID,
                  'message-container offer': message.offer !== null,
                }"
              >
                <div class="message-loader-container">
                  <div class="message-loader-wrapper">
                    <b-spinner
                      v-if="message.loader"
                      variant="primary"
                      type="grow"
                      label="Spinning"
                    ></b-spinner>
                  </div>
                </div>
                <time
                  :datetime="message.created_at"
                  v-if="message.sent_at && message.created_at"
                  >{{ message.sent_at }}</time
                >
                <article
                  :key="`message-${i}`"
                  class="message flex-column"
                  :class="{
                    'message-recived': message.user_id !== userID,
                    'message-sent': message.user_id === userID,
                    offer: message.offer !== null,
                  }"
                >
                  {{ message.message }}

                  <div v-if="message.offer">
                    <div class="row mx-0">
                      <div class="col-5">
                        <p class="font-weight-bold">
                          {{ $t('property.offerNight') }}
                        </p>
                      </div>
                      <div class="col-7">
                        <p
                          :class="[i !== 0 && 'font-weight-bold']"
                          :style="{
                            color: i !== 0 && '#28a745',
                          }"
                        >
                          {{
                            message.offer.details[i]
                              ? message.offer.details[i].offer_price
                              : message.offer.details[0].offer_price
                          }}
                          {{ $t('common.egp') }}
                        </p>
                      </div>
                    </div>
                    <div class="row mx-0">
                      <div class="col-5">
                        <p class="font-weight-bold">
                          {{ $t('property.guests') }}
                        </p>
                      </div>
                      <div class="col-7">
                        <p>
                          {{
                            message.offer.request.num_adult +
                            message.offer.request.num_children
                          }}
                          {{ $t('property.person') }}
                        </p>
                      </div>
                    </div>
                    <div class="row mx-0" v-if="message.offer.security_deposit">
                      <div class="col-5">
                        <p class="font-weight-bold">
                          {{ $t('property.securityDeposit') }}
                        </p>
                      </div>
                      <div class="col-7">
                        <p>
                          {{ message.offer.security_deposit }}
                          {{ $t('common.egp') }}
                        </p>
                      </div>
                    </div>

                    <div
                      class="row mx-0"
                      v-if="message.offer.request.move_in_date"
                    >
                      <div class="col-5">
                        <p class="font-weight-bold">
                          {{ $t('property.checkInDate') }}
                        </p>
                      </div>
                      <div class="col-7">
                        <p>
                          {{ message.offer.request.move_in_date }}
                        </p>
                      </div>
                    </div>
                    <div
                      class="row mx-0"
                      v-if="message.offer.request.move_out_date"
                    >
                      <div class="col-5">
                        <p class="font-weight-bold">
                          {{ $t('property.checkOutDate') }}
                        </p>
                      </div>
                      <div class="col-7">
                        <p>
                          {{ message.offer.request.move_out_date }}
                        </p>
                      </div>
                    </div>
                    <div
                      class="action-container"
                      v-if="i === allMessages.length - 1"
                    >
                      <!-- '0=>not accepted,1=>accepted,2=>counter offer,3=>not yet replied' -->
                      <!-- user is sending an offer actions shouldn't appear   -->
                      <template v-if="isLandlord">
                        <RespondActions
                          :offer-index="i"
                          :tenant-offer-price="

                            message.offer.details[i]
                              ? message.offer.details[i].offer_price
                              : message.offer.details[0].offer_price
                          "
                          :offer-price-term="message.offer.request.term"
                          :offer-id="message.offer.id"
                          :status="message.offer.status"
                          :rejection-reason="message.offer.rejection_reason"
                          @onSubmit="onSubmit"
                          :showStatus="message && message.user_id !== userID"
                        />
                      </template>
                      <template v-else>
                        <RespondActions
                          :offer-index="i"
                          :tenant-offer-price="
                            message.offer.details[i]
                              ? message.offer.details[i].offer_price
                              : message.offer.details[0].offer_price
                          "
                          :offer-price-term="message.offer.request.term"
                          :offer-id="message.offer.id"
                          :status="message.offer.status"
                          :rejection-reason="message.offer.rejection_reason"
                          @onSubmit="onSubmit"
                          :showStatus="message && message.user_id !== userID"
                        />
                      </template>
                    </div>
                  </div>
                </article>
              </div>
            </div>
          </div>
        </transition>
      </section>
      <button
        v-if="!scrolledToBottomChat && activeFriend"
        @click="scrollToBottomOfChat"
        class="btn btn-secondary rounded-circle go_bottom p-0"
      >
        <svg-icon
          name="arrow-right"
          class="banner-button-icon rotate-90"
          viewBox="0 0 20.4 20.4"
        />
        <span class="sr-only">{{ $t('common.scrollToTheEndOfTheChat') }}</span>
      </button>

      <section
        class="empty-state text-center"
        v-if="!activeFriend || !allFriends || !allFriends.length"
      >
        <svg-icon
          name="start-chatting"
          class="start-chatting"
          width="614.24714"
          height="528.65003"
          viewBox="0 0 614.24714 528.65003"
        />
        <article v-if="!activeFriend && allFriends && allFriends.length">
          <h2 class="my-3 font-weight-bold">
            {{ $t('message.emptyState.chooseFriend') }}
          </h2>
          <p class="font-small">
            {{ $t('message.emptyState.pickAFriendFromMenu') }}
          </p>
        </article>
        <article v-else>
          <h2 class="my-3 font-weight-bold">
            {{ $t('message.emptyState.heyNiceToHaveYou') }}
          </h2>
          <p class="font-small">
            {{ $t('message.emptyState.pleaseFeelFreeToBrowse') }}
          </p>
          <section class="actions">
            <NuxtLink
              :to="localePath('/properties/add/1')"
              class="text-decoration-none mb-2 btn btn-primary btn-block"
            >
              {{ $t('message.emptyState.addNewProperty') }}
            </NuxtLink>

            <NuxtLink
              :to="localePath('/')"
              class="text-decoration-none mb-2 btn btn-outline-primary btn-block"
            >
              {{ $t('message.emptyState.browseProperties') }}
            </NuxtLink>
          </section>
        </article>
      </section>

      <SendMessage
        @sendingMessage="sendingMessageHandler"
        @sentMessage="refetchMessages({ page: 1, per_page: 1 }, true)"
        v-if="activeFriend"
        :disabled="activeFriend.block === 0"
      />
    </b-overlay>
    <SideBar :friend="activeFriend" />
  </div>
</template>

<script>
import SendMessage from './SendMessage'
import SideBar from '@/components/messageSide/SideBar.vue'
import RespondActions from '@/components/shared/RespondActions'
import $axios from '~/helpers/axios-port'

export default {
  components: { SendMessage, SideBar, RespondActions },
  props: {
    allFriends: {
      type: Array,
      required: false,
      default: null,
    },
    recentMessage: {
      type: Object,
      required: false,
      default: null,
    },
    messages: {
      type: Array,
      required: false,
      default: null,
    },
    dataLoading: {
      type: Boolean,
      required: false,
      default: false,
    },
  },
  data() {
    return {
      page: 2,
      per_page: 10,
      dataComplete: false,
      scrolledToBottomChat: false,
      activeFriend: null,
      allMessages: [],
    }
  },
  mounted() {
    setTimeout(() => {
      this.scrollToBottomOfChat()
    }, 1000)
  },
  created() {},
  computed: {
    loadingMessages() {
      return this.$store.state.MessagingModule.loadingMessages
    },
    messagesMeta() {
      return this.$store.state.MessagingModule.messagesMeta
    },
    chats() {
      return this.$store.state.MessagingModule?.messages?.Data?.chats
    },
    userID() {
      return this.$store.state.UserModule?.user?.user_id
    },
    isLandlord() {
      return this.$store.state.UserModule?.user?.isLandlord
    },
    activeChat() {
      return this.$store.state.MessagingModule.activeChat
    },
  },
  filters: {
    excerpt: function (value) {
      if (!value) return ''
      return value.toString().slice(0, 50) + ' ' + ' ...'
    },
  },
  watch: {
    allFriends: {
      handler(value) {
        if (value) {
          this.activeFriend = this.allFriends?.find(
            (el) =>
              el.id === this.activeChat?.friend_id &&
              el.property_id === this.activeChat?.property_id
          )
          if (this.activeFriend) {
            this.$store.dispatch(
              'PropertyModule/setActiveChatFriend',
              this.activeFriend
            )
          }
        }
      },
      immediate: true,
    },
    recentMessage: {
      handler: function (val) {
        if (!val || !val.message) return
        this.refetchMessages()
      },
      deep: true,
    },
    activeFriend: function (val) {
      this.page = 2
    },
    activeChat: function (val) {
      this.refetchMessages()
    },
    messages: function (val) {
      this.allMessages = this.messages
      this.scrollToBottomOfChat(null, null, 200)
      this.dataLoading = false
    },
    loadingMessages: function (val) {},
  },
  methods: {
    refetchMessages(
      param = null,
      isLastMessage = false,
      infiniteScroll = false
    ) {
      if (!this.activeChat) return
      var self = this

      $axios
        .$get(
          `/chat/session/${this.activeChat?.session_id}/${this.activeChat?.property_id}/chats`
        )
        .then((res) => {
          var objIndex = self.allFriends.findIndex(
            (obj) => obj.id === self.activeFriend
          )
          // self.allFriends[objIndex].unreadCount = 0
          // self.allFriends[objIndex].session.unreadCount = 0
          self.allMessages = res.Data.data
          if (infiniteScroll) {
            this.page++
            this.scrollToBottomOfChat()
            if (
              this.allMessages &&
              this.messagesMeta &&
              (this.allMessages.length === this.messagesMeta.total ||
                this.page > this.messagesMeta.pages_count)
            ) {
              this.dataComplete = true
            }
          } else {
            this.scrollToBottomOfChat()
          }
        })
    },
    onSubmit() {
      this.$nextTick(() => {
        window.location.reload()
      })
    },
    sendingMessageHandler(message) {
      this.allMessages.push({
        message: message,
        created_at: new Date().toJSON().slice(0, 10).replace(/-/g, '-'),
        sent_at: 'now',
        type: 0,
        loader: true,
        offer: null,
        user_id: this.userID
      })
    },
    infiniteScrollHandler(el) {
      if (
        el.srcElement.offsetHeight + el.srcElement.scrollTop >=
        el.srcElement.scrollHeight - 100
      ) {
        this.scrolledToBottomChat = true
      } else {
        this.scrolledToBottomChat = false
      }
      if (this.dataComplete || !this.messagesMeta) return
      if (el.srcElement.scrollTop > 0) return
      this.dataLoading = true

      this.refetchMessages(null, false, true)
    },
    displayList() {
      if (window.innerWidth <= 767) {
        document.querySelector('.left-side').classList.remove('d-none')
        document.querySelector('.right-side').classList.add('d-xs-none')
      }
    },
    scrollToBottomOfChat(e, pixels = null, timeout = 0) {
      var self = this
      setTimeout(function () {
        const elem = self.$refs.body
        if (!elem) return

        this.scrolledToBottomChat = true

        elem.scrollTo({
          top: pixels ? pixels : elem.scrollHeight - elem.clientHeight,
          behavior: 'smooth',
        })
      }, timeout)
    },
  },
}
</script>

<style lang="scss" scoped>
.d-xs-none {
  @media screen and (max-width: 767px) {
    display: none;
  }
}
.empty-state {
  position: absolute;
  top: 0;
  margin-left: auto;
  margin-right: auto;
  width: 90%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  height: 100%;
  .text-decoration-none {
    text-decoration: none;
  }
  .start-chatting {
    width: 200px;
    height: 200px;
  }

  h2 {
    font-size: 15px;
  }
  p {
    // font-size: 13px;
    max-width: 40ch;
    line-height: 1.2rem;
  }
}

.user-heading::v-deep() {
  .user-image {
    position: relative;
    padding-top: 100%;
    & > img {
      border-radius: 50%;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      max-width: 50px;
      max-height: 50px;
    }
  }
}

.user-heading::v-deep() {
  // height: 60px;
  border-bottom: 1px solid $grayBackgroundColor;
}
.reading-message {
  height: 385px;
  @media screen and (min-width: 767px) {
    height: 60vh;
  }
  overflow-y: auto;
  padding: 10px 10px 10px 0;
  &::-webkit-scrollbar {
    width: 5px;
  }

  &::-webkit-scrollbar-track {
    background: #fff;
  }

  &::-webkit-scrollbar-thumb {
    background: $grayBackgroundColor;
  }

  &::-webkit-scrollbar-thumb:hover {
    background: $grayBackgroundColor;
  }
  .message-container {
    text-align: end;
    margin-bottom: 35px;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    padding: 0 5px;
    .message-loader-container {
      height: 100%;
      position: relative;
      .message-loader-wrapper {
        margin-bottom: 50%;
        transform: translateY(25%);
      }
    }
    &.message-sent{
      time{
        text-align: right;
      }
    }
    time {
      width: 120px;
      color: $secondaryTextColor;
      margin: 0 10px;
      flex: 1;
    }
    &.message-received {
      flex-direction: row-reverse;
      text-align: end;
      justify-content: flex-start;
      time {
        text-align: start;
      }
    }
    .message {
      max-width: 75%;
      width: auto;
      padding: 10px;
      border-radius: 5px;
      font-size: 14px;
      position: relative;
      flex: 1;
      &:not(.offer) {
        display: inline-block !important;
        min-width: 50%;
        text-align: start;
      }
    }
    .message-sent {
      background: $mainColorLight;
      color: #fff;
      &.offer {
        background: $grayBackgroundColor;
        color: #000;
      }
      display: flex;
      margin-left: auto;
      margin-right: 10px;
      time {
        left: 0;
        bottom: 0;
        transform: translateX(-120px);
        text-align: center;
        @media screen and (max-width: 767px) {
          transform: translateY(25px);
        }
      }
    }
    .message-recived {
      display: flex;
      margin-right: auto;
      margin-left: 10px;
      background: $grayBackgroundColor;
      color: #000;
      time {
        right: 0;
        bottom: 0;
        transform: translateX(130px);
        @media screen and (max-width: 767px) {
          transform: translateY(25px);
        }
      }
    }
  }
}

.go_bottom {
  bottom: 85px;
  left: 20px;
  position: absolute;
  width: 30px;
}

.rotate-90 {
  transform: rotate(90deg);
}
.location {
  color: $mainTextColor;
}
.name {
  font-size: 12px;
  color: $secondaryTextColor;
  margin-bottom: 8px;
}
.font-14 {
  font-size: 14px;
}

::v-deep() .actions {
  @include media-breakpoint-down(sm) {
    margin-bottom: 0;
    flex-direction: column;
    button.btn.action-btn {
      margin-bottom: 10px !important;
      width: 100%;
    }
    & > div {
      width: 100%;
      & > span {
        width: 100%;
      }
    }
  }
}

.cursor-pointer {
  cursor: pointer;
}
</style>
