<template>
  <div>
    <b-modal
      ref="reject-offer-modal"
      v-bind="$attrs"
      centered
      hide-header
      hide-footer
      v-model="rejectOfferModel"
      @hidden="$emit('update:rejectOfferModel')"
    >
      <ValidationObserver v-slot="{ handleSubmit }">
        <b-form @submit.prevent="handleSubmit(rejectOffer)">
          <b-overlay
            :show="
              respondActionLoading.type == 'reject' &&
              respondActionLoading.loading == true
            "
          >
            <header class="d-flex align-items-center">
              <h2 class="title-modal">{{ $t('offer.rejectionReason') }}</h2>
            </header>
            <b-row>
              <b-col cols="12">
                <span class="small-text-modal grey-text-modal">{{
                  $t('offer.rejectionReasonHint')
                }}</span>
                <div class="small-text-modal">
                  {{ $t('offer.offerIsLow') }}
                  <app-button
                    :label="$t('offer.sendCounterOffer')"
                    variant="link"
                    class="small-text-modal send-counterOffer-btn"
                    @click="
                      $bvModal.hide('reject-offer-modal')
                      $bvModal.show(`counter-offer-modal-${offerId}`)
                    "
                  />
                </div>

                <b-form-radio
                  v-model="form.rejection_object"
                  class="reject-reason"
                  name="reject-reason"
                  :value="{
                    rejection_id: reason.id,
                    rejection_reason: reason.name,
                  }"
                  v-for="(reason, index) in rejectionReasons"
                  :key="index"
                  plain
                  size="lg"
                  >{{ reason.name }}</b-form-radio
                >
                <div class="button-container">
                  <app-button
                    type="submit"
                    :class="['submit-offer', { danger: state }]"
                    :label="$t('offer.reject')"
                    :disabled="!state"
                  />
                </div>
              </b-col>
            </b-row>
          </b-overlay>
        </b-form>
      </ValidationObserver>
    </b-modal>
  </div>
</template>

<style lang="scss" scoped>
::v-deep() .modal-dialog {
  max-width: 370px;
  margin: auto;
}
.title-modal {
  font-size: 18px;
  font-weight: bold;
}
.grey-text-modal {
  color: $secondaryTextColor;
}
.small-text-modal {
  font-size: 12px;
}
.send-counterOffer-btn {
  padding: 14px 0px;
}
.reject-reason {
  font-size: 14px;
  margin: 0px 0px 20px 0px;
  [dir='rtl'] & {
    padding-right: 1.25rem;
  }
  ::v-deep() input {
    [dir='rtl'] & {
      margin-right: -1.25rem;
    }
  }
}
.button-container {
  display: flex;
  justify-content: flex-end;
  .submit-offer {
    margin-top: 20px;
    width: 94px;
    height: 38px;
    border: 0;
    font-size: 14px;
  }
  .danger {
    background: #dc3545;
    color: #fff;
  }
}
.notes-container {
  display: flex;
  justify-content: flex-end;
  .notes-input {
    border-right: 0;
    border-left: 0;
    border-top: 0;
    border-radius: 0;
    max-width: 96%;
  }
}
</style>
<script>
import { createNamespacedHelpers } from 'vuex'
const { mapActions, mapState } = createNamespacedHelpers('PropertyModule')
export default {
  name: 'RejectOfferModal',
  props: {
    offerId: {
      type: String | Number,
      required: true,
    },
    rejectOfferModel: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      form: {
        rejection_object: {},
        notes: '',
      },
    }
  },
  methods: {
    ...mapActions(['getRejectionReasons']),
    rejectOffer() {
      this.$emit('reject', this.form)
    },
    resetModal() {
      this.form.rejection_object = {}
      this.form.notes = ''
    },
  },
  mounted() {
    this.getRejectionReasons()
  },
  computed: {
    ...mapState(['rejectionReasons', 'respondActionLoading']),
    ...mapState({
      visibility: (state) => state.respondActionModalVisibility.visibility,
      respondType: (state) => state.respondActionModalVisibility.type,
    }),
    state() {
      return Boolean(this.form.rejection_object.rejection_id)
    },
  },
  watch: {
    visibility(newVal) {
      newVal && this.respondType == 'reject'
        ? this.$refs['reject-offer-modal'].show()
        : this.$refs['reject-offer-modal'].hide()
    },
  },
}
</script>
